// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";

// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";

// Material Kit 2 React examples
import DefaultInfoCard from "../Cards/InfoCards/DefaultInfoCard";
import BackgroundBlogCard from "../Cards/BackgroundBlogCard";
import backgroundCard from "assets/images/bg-coworking.jpeg"
// Images


function Information(props) {
      
  return (
    <MKBox component="section" py={6} my={6}>
      <Container>
      <MKBox component="section" my={1} py={3}>
         <MKTypography variant="h2" fontWeight="bold">
           Various support services for any needs
         </MKTypography>
         <MKTypography variant="body1" color="text">
           We have created multiple options for you to feel safe and comfortable for you and those you love. 
         </MKTypography>
      </MKBox>
        <Grid container item xs={11} spacing={3} alignItems="center" sx={{ mx: "auto" }}>
          <Grid item xs={12} lg={4} sx={{ mx: "auto" }}>
          <BackgroundBlogCard
                image={backgroundCard}
              />
          </Grid>
          <Grid item xs={12} lg={7} sx={{ ml: "auto" }}>
          <Grid  alignItems="center">

         
            <Grid container spacing={3}>
              <Grid item xs={12} md={6}>
                <DefaultInfoCard
                  icon="meeting_room"
                  title="Talking Space"
                  description="Talking space that provides comfort for any individual or groups. Take time to collect your thoughts and provide 
                  enough distance between each other in a private space."
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <DefaultInfoCard
                  icon="grass"
                  title="Healing Garden"
                  description="Gardens provide a place to promote healing. Any environment can promote healing,
                  but gardens are particularly able to do so because 
                  as humans, we find nature engrossing and soothing."
                />
              </Grid>
            </Grid>
         
            <Grid container spacing={3} sx={{ mt: { xs: 0, md: 6 } }}>
              <Grid item xs={12} md={6}>
                <DefaultInfoCard
                  icon="people_outline"
                  title="Marriage and Couples Counseling"
                  description="Find ways to communicate with each other and overcome challenges that often come with relationships. "
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <DefaultInfoCard
                  icon="escalator_warning"
                  title="Child Behavioral Sessions"
                  description="Receive emotional support to help understand the main problems in a protective environment."
                />
              </Grid>
            </Grid>
          </Grid>
          </Grid>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default Information;
