import React, {useCallback, useReducer} from 'react';
import DefaultNavbar from "../components/Navbars/index";
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";
import MKAvatar from "components/MKAvatar";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import MKButton from "components/MKButton";

// @mui material components
import team1 from "assets/images/team-5.jpg";
import SittingGirl from "assets/images/SittingGirl.jpg";

import DemoFooter from "../components/Footer/DemoFooter";

// Routes
import routes from "routes";
//images
import bg2 from "../assets/images/bg2.jpg";

const AboutUs = (props) =>{
;
      return (
        <>
     <DefaultNavbar
        routes={routes}
        sticky
        transparent
        light
      />
      <MKBox bgColor="white">
      <MKBox
          minHeight="25rem"
          width="100%"
          sx={{
            backgroundImage: ({ functions: { linearGradient, rgba }, palette: { gradients } }) =>
              `${linearGradient(
                rgba(gradients.dark.main, 0.8),
                rgba(gradients.dark.state, 0.8)
              )}, url(${bg2})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            display: "grid",
            placeItems: "center",
          }}
        >
         <MKTypography
        variant="h1"
        color="white"
        mt={-6}
        mb={1}
        sx={({ breakpoints, typography: { size } }) => ({
          [breakpoints.down("md")]: {
            fontSize: size["3xl"],
          },
        })}
      >
        About Us{" "}
      </MKTypography>
      </MKBox>
         <MKBox component="section" py={{ xs: 6, sm: 12 }}>
          <Container>
          <Grid container item xs={12} justifyContent="center" py={6} mx="auto">
              <MKBox mt={{ xs: -16, md: -20 }} textAlign="center">
                <MKAvatar src={SittingGirl} alt="Burce Mars" size="xxl" shadow="xl" />
             </MKBox>
             <Grid container justifyContent="center" py={6}>
              {/* <MKBox display="flex" justifyContent="space-between" alignItems="center" mb={1}>
                <MKTypography variant="h3">About Us</MKTypography>
              </MKBox> */}             
              <MKTypography variant="body1" fontWeight="light" color="text">
                Decisions: If you can&apos;t decide, the answer is no. If two equally difficult
                paths, choose the one more painful in the short term (pain avoidance is creating an
                illusion of equality). Choose the path that leaves you more equanimous. <br />
               
              </MKTypography>
            </Grid>
          </Grid>
          </Container>
        </MKBox>
        </MKBox>
        <MKBox pt={6} px={1} mt={6}>
             <DemoFooter  />
        </MKBox>
        </>
    )
};

export default AboutUs;