import { useNavigate } from "react-router-dom";
import { useState } from "react";
// @mui material components
import Grid from "@mui/material/Grid";

// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKButton from "components/MKButton";
import MKTypography from "components/MKTypography";
import {useForm} from "../hooks/form-hook";
import InputForm from "../components/InputForm/InputForm";
// Material Kit 2 React examples
import DefaultNavbar from  "../components/Navbars/index";
import DemoFooter from "../components/Footer/DemoFooter";
import MKAlert from "components/MKAlert";

// Routes
import routes from "routes";

// Image
import contactImg from "assets/images/presentation/girlOnPhone.jpg"

const ContactUs =(props) =>{
  let navigate = useNavigate();
  const [error, setError] = useState(false);

  const [formState, inputHandler] = useForm({
      email:{
      value:'',
      isValid: false
    },
      message:{
      value:'',
      isValid: false
    },
    fullName: {
      value:'',
      isValid: false
    },
    phoneNumber:{
      value:'',
      isValid: false
    },
    subject:{
      value:'',
      isValid: false
    }}
  , false);

  const formSubmitHandler = async(event) =>{
    event.preventDefault();
    try{
      const formData = new FormData();
      formData.append('fullName', formState.inputs.fullName.value);
      formData.append('phoneNumber', formState.inputs.phoneNumber.value);
      formData.append('subject', formState.inputs.subject.value);
      formData.append('message', formState.inputs.message.value);
      formData.append('email', formState.inputs.email.value);


         await fetch(`${process.env.REACT_APP_BACKEND_URL}/contact/sendEmail`, {
       method: 'POST',
       body: formData
     }).then(response =>{
      if(!response.ok){
        setError(true);
        throw new Error("Error in data")
      }else{
        const responseData =  response.json();
        if(response.ok){
          navigate("/")
        }
        console.log(responseData);
      }
     }
     ).catch(err =>{
       console.log(error)
     });
  
        }catch(err){
  }
}

  return (
    <>
      <MKBox position="fixed" top="0.5rem" width="100%">
        <DefaultNavbar
          routes={routes}
        />
      </MKBox>
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12} lg={6}> 
          <MKBox
            display={{ xs: "none", lg: "flex" }}
            width="calc(100% - 2rem)"
            height="calc(100vh - 2rem)"
            borderRadius="lg"
            ml={2}
            mt={2}
          //   sx={{ backgroundImage: `url(${contactImg})`, 
          //   backgroundSize: "cover",
          //   backgroundPosition: "top",

          // }}
          sx={{
            backgroundImage: ({ functions: { linearGradient, rgba }, palette: { gradients } }) =>
              `${linearGradient(
                rgba(gradients.dark.main, 0.8),
                rgba(gradients.dark.state, 0.2)
              )}, url(${contactImg})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
            display: "grid",
            placeItems: "center",
          }}
          />
        </Grid>
        <Grid
          item
          xs={12}
          sm={10}
          md={7}
          lg={6}
          xl={4}
          ml={{ xs: "auto", lg: 6 }}
          mr={{ xs: "auto", lg: 6 }}
        >
          <MKBox
            bgColor="white"
            borderRadius="xl"
            shadow="lg"
            display="flex"
            flexDirection="column"
            justifyContent="center"
            mt={{ xs: 20, sm: 18, md: 20 }}
            mb={{ xs: 20, sm: 18, md: 20 }}
            mx={3}
          >
            <MKBox
              variant="gradient"
              bgColor="secondary"
              coloredShadow="info"
              borderRadius="lg"
              p={2}
              mx={2}
              mt={-3}
            >
              <MKTypography variant="h3" color="white">
                Contact us
              </MKTypography>
            </MKBox>
            <MKBox p={3}>
              <MKTypography variant="body2" color="text" mb={3}>
                When you are ready to reach out,please email 
                hello@ct.com or contact using our contact form. 
              </MKTypography>
              {/* <MKBox width="100%" component="form" onSubmit={formSubmitHandler} method="post" autocomplete="off"> */}
               <form onSubmit={formSubmitHandler}>
                <Grid container spacing={3}>
                  <Grid item xs={12} md={6}>
                     <InputForm
                      id="fullName"
                      label="Full Name"
                      InputLabelProps={{ shrink: true }}
                      fullWidth
                      onInput={inputHandler}
                      type="text"
                      element="input"
                      variant="outlined"
                      required
                      />
                  </Grid>
                  <Grid item xs={12} md={6}>
                     <InputForm
                      id="email"
                      label="Email"
                      InputLabelProps={{ shrink: true }}
                      fullWidth
                      onInput={inputHandler}
                      type="email"
                      element="input"
                      variant="outlined"
                      required
                      />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <InputForm element="input"
                      id="phoneNumber" 
                      type="text" label="Phone Number"
                      variant="outlined"
                      placeholder="Phone Number"
                      InputLabelProps={{ shrink: true }}
                      fullWidth  
                      onInput={inputHandler}       
                      required 
                      minLength={10}
                      inputProps={{ inputMode: 'numeric', pattern: '[0-9]*', minLength:10, maxLength:15  }}                      helperText="Format: 0000000000"  
                    />
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <InputForm element="input"
                      id="subject"
                      type="text"
                      variant="outlined"
                      label="Subject"
                      InputLabelProps={{ shrink: true }}
                      fullWidth
                      required
                      onInput={inputHandler}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <InputForm
                      id="message"
                      variant="outlined"
                      label="Message"
                      InputLabelProps={{ shrink: true }}
                      multiline
                      fullWidth
                      rows={6}
                      onInput={inputHandler}
                      required
                    />
                  </Grid>
                  <Grid container item justifyContent="center" xs={12} mt={5} mb={2}>
                  <MKButton type="submit" variant="gradient" color="secondary">
                    Send Message
                  </MKButton>
                </Grid>
                </Grid>
             
                </form>
                {
       error && <MKAlert color="light"  >Something went wrong. Please check inputs and try again.</MKAlert>
    }            </MKBox>
          </MKBox>
        </Grid>
      </Grid>
         <MKBox pt={6} px={1} mt={6}>
             <DemoFooter  />
        </MKBox>
    </>
  );
}

export default ContactUs;
